<template>
    <div class="brightness">
        <navigate title="亮度设置" position="absolute"/>
        <div class="scroll-wrapper">
            <div class="brightness-content">
                <div class="item title border-1px-b">
                    <span>屏幕亮度</span>
                    <span class="tip" @click="defaultBrightness" :if="tip">跟随系统</span>
                </div>
                <div class="item">
                    <mu-slider class="slider" :value="brightness" @change="change" :step="1"/>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import Navigate from "../../../base/navigate/navigate"
    import Cordova from '../../../common/js/cordova'
    import {appMixins} from "../../../common/mixins"

    export default {
        name: "brightness",
        mixins: [appMixins],
        data() {
            return {
                tip: false
            }
        },
        created() {
            Cordova.brightness.get().then(brightness => {
                console.log(`%c当前屏幕亮度为 ${brightness}`, 'color: #673ab7')
                if (brightness > 0) {
                    this.tip = true

                    this.setBrightness(Math.round(brightness * 100))
                }
            })
        },
        methods: {
            change(value) {
                Cordova.brightness.set(value).then(status => {
                    this.setBrightness(value)
                })
            },
            defaultBrightness() {
                Cordova.brightness.set(-1).then(status => {
                    this.tip = false

                    this.setBrightness(0)
                })
            }
        },
        components: {
            Navigate
        }
    }
</script>

<style lang="stylus" scoped>
    @import "../../../common/stylus/variable.styl"

    .brightness
        padding-top 46px
        background-color: #efefef
        .scroll-wrapper
            padding-top 15px
            width 100%
            height 100%
            .brightness-content
                background-color #fff
                .item
                    display flex
                    align-items center
                    padding 0 15px
                    height 50px
                    &.title
                        display flex
                        align-items center
                        justify-content space-between
                        .tip
                            color #1976d2
                            font-size $font-size-sl
                    .slider
                        margin 0
</style>



